import {
  deleteAll,
  deleteItem,
  getHistory,
  initStorage,
  saveHistory,
} from "../utils/storage";
import SearchPanel from "./SearchPanel/searchPanel";
import { useEffect, useState } from "react";
import { Helmet } from "react-helmet";
import SearchHistory from "./SearchHistory/searchHistory";

const SearchApp = () => {
  //* State and Setter for Search history
  const [searchHistory, setSearchHistory] = useState(getHistory());

  //* Add new Item in Search history then get new history
  const handleAddNewHistoryItem = (item) => {
    saveHistory(item);
    setSearchHistory(getHistory());
  };

  //* Delete function for removing an item from search history
  const handleDeleteSearchHistoryItem = (item) => {
    deleteItem(item).then(setSearchHistory(getHistory()));
  };

  //* Clear all history
  const handleDeleteAllSearchHistory = () => {
    deleteAll().then(setSearchHistory(getHistory()));
  };

  const [showHistoryPanel, setShowHistoryPanel] = useState(false);

  useEffect(() => {
    initStorage();
  }, []);

  return (
    <section className="col-11 col-s-10 col-m-10 col-l-6">
      <Helmet>
        <title>Soltia Simple Search | AutoComplete</title>
      </Helmet>
      <SearchPanel
        onAddHistoryItem={handleAddNewHistoryItem}
        onDeleteSearchHistoryItem={handleDeleteSearchHistoryItem}
        onChangeHistoryPanelStatus={setShowHistoryPanel}
      />
      <SearchHistory
        history={searchHistory}
        onDeleteHistoryItem={handleDeleteSearchHistoryItem}
        onDeleteAll={handleDeleteAllSearchHistory}
        showHistory={showHistoryPanel}
      />
    </section>
  );
};

export default SearchApp;
