import { currentDate, currentTime } from "./dateFactory";

//* Define a key for search history.
const searchHistoryKey = "SSA_SearchHistory";

//* Define a key for search history length.
const searchHistoryLength = "SSA_SearchHistoryLength";

//* Define a key for suggestion count .
const suggestionCount = "SSA_SuggestionCount";

//* Define a key for  history count on suggestion list .
const historyCountForSuggestionList = "SSA_HistoryCountForSuggestionList";

//* Setting default value .
export const initStorage = () => {
  if (!localStorage.getItem(searchHistoryLength))
    localStorage.setItem(searchHistoryLength, 20);

  if (!localStorage.getItem(suggestionCount))
    localStorage.setItem(suggestionCount, 10);

  if (!localStorage.getItem(historyCountForSuggestionList))
    localStorage.setItem(historyCountForSuggestionList, 5);
};

