//* Return search history length.
export const getHistoryCount = () => {
  return parseInt(localStorage.getItem(searchHistoryLength));
};

//* Return suggestion count.
export const getSuggestionCount = () => {
  return parseInt(localStorage.getItem(suggestionCount));
};

//* Return history count for suggestion list.
export const getHistoryOnListCount = () => {
  return parseInt(localStorage.getItem(historyCountForSuggestionList));
};

//* Return unique id.
const generateId = (term) => {
  return (
    Math.floor(1 + Math.random() * 1000) + term.length * new Date().getSeconds()
  );
};

//* Save a word in search history.
export const saveHistory = (term) => {
  let history = getHistory();

  const newTerm = {
    id: generateId(term),
    word: term,
    date: currentDate(),
    time: currentTime(),
  };

  if (history) {
    if (history.length === getHistoryCount()) history.shift();
    history.push(newTerm);
  } else history = [{ ...newTerm }];
  localStorage.setItem(searchHistoryKey, JSON.stringify(history));
};

//* Return search history.
export const getHistory = () => {
  return JSON.parse(localStorage.getItem(searchHistoryKey)) || [];
};

//* Delete a word from history.
export const deleteItem = (deleteItem) => {
  return new Promise((resolve, reject) => {
    try {
      const history = getHistory().filter((item) => item.id !== deleteItem.id);
      localStorage.setItem(searchHistoryKey, JSON.stringify(history));
      resolve(true);
    } catch (ex) {
      reject(false);
    }
  });
};

//* Delete all data
export const deleteAll = () => {
  return new Promise((resolve, reject) => {
    try {
      const history = getHistory();
      history.length = 0;
      localStorage.setItem(searchHistoryKey, JSON.stringify(history));
      resolve(true);
    } catch (ex) {
      reject(false);
    }
  });
};
