import { useEffect, useState, useRef } from "react";
import BeatLoader from "react-spinners/BeatLoader";
import { useDebounce } from "../../hooks/useDebounce";
import { findWord } from "../../service/wordService";
import { toast } from "react-toastify";
import WordsList from "../WordsList/wordsList";

import "./searchPanel.css";
import { getHistory, getHistoryOnListCount } from "../../utils/storage";
import SearchVoiceBox from "../SearchVoiceBox/searchVoiceBox";

const SearchPanel = ({ onAddHistoryItem, onDeleteSearchHistoryItem }) => {
  //* Defind State and Setter for searchTerm.
  //* searchTerm : Use for holding a term that user want search.
  const [searchTerm, setSearchTerm] = useState("");

  //* Defind State and Setter for doSearch.
  //* doSearch : Use for Starting search or not.
  const [doSearch, setDoSearch] = useState(true);

  //* Defind State and Setter for wordList.
  //* wordList : Use for showing  searched information or history.
  const [words, setWords] = useState([]);

  //* Defind State and Setter for showWordList.
  //* showWordList : Use for words list can be shown or not.
  const [showWordList, setShowWordList] = useState(false);

  //* Use debouncedSearchTerm which is a custome hook that holds the search term for 500 ms
  //* then if user doesn't type or say anything ,sends the search term to API for searching.
  const debouncedSearchTerm = useDebounce(searchTerm, 500);

  //* Use useRef hook for accessing to input element
  const searchInput = useRef(null);
  //* Use useRef hook for accessing to search box element
  const searchBox = useRef(null);

  //* State and Setter for wordsListType .
  //* wordsListType : Use for specify word list type (searched or history).
  const [wordsListType, setWordsListType] = useState("searched");

  //* State and Setter for activedIndex.
  //* activedIndex :  The index item of word list which is selected.
  const [activedIndex, setActivedIndex] = useState(0);

  //* State and Setter for searchLoading.
  //* searchLoading : use for activing or disactiving loading.
  const [searchLoading, setSearchLoading] = useState(false);

  //* side effect here on change of debouncedSearchTerm.
  useEffect(() => {
    if (doSearch) search(debouncedSearchTerm);
    // setDoSearch(true);
  }, [debouncedSearchTerm, doSearch]);

  //* side effect here on change of debouncedSearchTerm.
  useEffect(() => {
    if (showWordList) searchBox.current.classList.add("open");
    else searchBox.current.classList.remove("open");
  }, [showWordList]);

  //---------------------------------------------
  //For search and suggestions in Auto-Complete component used datamuse’s API.
  //Get a suggestion list by API
  //Using datamuse’s API
  //Input parameter: searchTerm: the search item
  //Output parameter: data
  //---------------------------------------------
  const search = async (searchTerm) => {
    if (searchTerm) {
      try {
        setSearchLoading(true);

        setWords([]);
        const { data } = await findWord(searchTerm);
        setWords(data);
        setWordsListType("searched");
        setShowWordList(true);
        setSearchLoading(false);
      } catch (ex) {
        setShowWordList(false);
        setWords([]);
        toast.error(ex.meesage);
        setSearchLoading(false);
      }
    } else {
      setShowWordList(false);
      setWords([]);
    }
  };

  //* Handle Actions when a user clicks on input box
  const handleSearchInputClick = (value) => {
    setActivedIndex(0);
    const history = getHistory();
    if (history && history.length > 0) {
      setShowWordList(true);
      setWords(history.slice(0, getHistoryOnListCount()));

      setWordsListType("history");
    }
  };

  //* Handle Actions when a user presses on input box
  const handleKeyDown = (e) => {
    switch (e.key) {
      case "Escape":
        setSearchTerm("");
        setShowWordList(false);
        break;
      case "Enter":
        onAddHistoryItem(e.target.value);
        setShowWordList(false);
        break;
      case "ArrowUp":
        setDoSearch(false);
        if (!showWordList) {
          handleSearchInputClick(null);
        }

        if (activedIndex === 0) {
          setActivedIndex(words.length - 1);
          return;
        }
        setActivedIndex(activedIndex - 1);
        setSearchTerm(words[activedIndex - 1].word);
        break;
      case "ArrowDown":
        setDoSearch(false);
        if (!showWordList) {
          handleSearchInputClick(null);
        }
        if (activedIndex === words.length - 1) {
          setActivedIndex(0);
          return;
        }

        if (activedIndex < words.length - 1) {
          setActivedIndex(activedIndex + 1);
          setSearchTerm(words[activedIndex + 1].word);
        }
        break;
      default:
        setDoSearch(true);
        break;
    }
  };

  //* Selecting an item from searched words
  const handleWordsListItemClick = (term) => {
    onAddHistoryItem(term);
    setShowWordList(false);
    searchInput.current.focus();
  };

  //* Every time a user says a phrase, it invokes search.
  const searchVoiceTerm = (term) => {
    if (term) {
      setDoSearch(true);
      setSearchTerm(term);
      searchInput.current.focus();
      onAddHistoryItem(term);
    }
  };

  return (
    <>
      <section
        role="search"
        className="search-box"
        ref={searchBox}
        onBlur={() => setShowWordList(false)}
      >
        <div className="search-fields">
          <span className="search-logo">
            <i
              className="fa fa-search search-icon"
              aria-hidden="true"
              title="Search icon"
            ></i>
          </span>
          <input
            ref={searchInput}
            autoFocus
            id="input"
            type="search"
            autoComplete="off"
            spellCheck="false"
            role="searchbox"
            name="q"
            aria-label="Search"
            aria-live="polite"
            className="search-input"
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            onClick={(e) => handleSearchInputClick(e.target.value)}
            onKeyDown={handleKeyDown}
            placeholder="Search for..."
            list="words"
          />
          <BeatLoader
            loading={searchLoading}
            css="width:5rem;text-align:right"
            color={"#ffaf00"}
            size={7}
          />
          <SearchVoiceBox onSearchTerm={searchVoiceTerm} />
        </div>
        <WordsList
          showList={showWordList}
          wordList={words}
          wordListType={wordsListType}
          onItemClick={handleWordsListItemClick}
          onDeleteSearchHistoryItem={onDeleteSearchHistoryItem}
          inputValue={searchTerm}
          activeIndex={activedIndex}
        />
      </section>
    </>
  );
};

export default SearchPanel;
