import "./wordsList.css";
const WordsList = ({
  wordList,
  inputValue,
  wordListType,
  activeIndex,
  showList,
  onDeleteSearchHistoryItem,
  onItemClick,
}) => {
  return (
    <>
      {showList && (
        <ul className="words-list">
          {wordList.map((item, index) => (
            <li
              key={index}
              className={`words-list-item ${
                activeIndex === index ? "active" : null
              }`}
            >
              <div
                className="word-data-wrapper"
                onMouseDown={() => onItemClick(item.word)}
              >
                <i
                  className={`fa ${
                    wordListType === "searched" ? "fa-search" : "fa-history"
                  } word-icon`}
                  aria-hidden="true"
                  title="Item kind icon"
                />
                {item.word.substr(0, inputValue.length) === inputValue ? (
                  <p className="word-text">
                    <strong className="word-text-highlight">
                      {item.word.substr(0, inputValue.length)}
                    </strong>
                    {item.word.substr(inputValue.length, item.word.length)}
                  </p>
                ) : (
                  <p className="word-text">{item.word}</p>
                )}
              </div>
              {wordListType === "history" ? (
                <button
                  type="button"
                  className="fa fa-times word-delete-btn"
                  title="Delete item"
                  onMouseDown={() => onDeleteSearchHistoryItem(item)}
                />
              ) : null}
            </li>
          ))}
        </ul>
      )}
    </>
  );
};

export default WordsList;
