import "./searchHistory.css";
import { useEffect, useRef, useState } from "react";
const SearchHistory = ({ history, onDeleteHistoryItem, onDeleteAll }) => {
  const historyPanel = useRef(null);
  const [showSearchHistory, setShowSearchHistory] = useState(false);
  const [width, setWidth] = useState(window.innerWidth);
  useEffect(() => {
    if (showSearchHistory) historyPanel.current.style.left = "0px";
    else
      historyPanel.current.style.left = `-${
        historyPanel.current.offsetWidth - 47 + 3
      }px`;
  }, [showSearchHistory]);

  useEffect(() => {
    const handleWindowResize = () => setWidth(window.innerWidth);
    window.addEventListener("resize", () => setWidth(window.innerWidth));
    return () => window.removeEventListener("resize", handleWindowResize);
  }, []);

  useEffect(() => {
    if (showSearchHistory) setShowSearchHistory(false);
    else
      historyPanel.current.style.left = `-${
        historyPanel.current.offsetWidth - 47 + 3
      }px`;
  }, [width]);

  return (
    <aside ref={historyPanel} className="history col-l4 col-m-5 col-s-6 col-12">
      <nav className="history-nav">
        <button
          type="button"
          className="history-btn"
          onClick={() => setShowSearchHistory(!showSearchHistory)}
        >
          <i
            className={showSearchHistory ? "fa fa-folder-open" : "fa fa-folder"}
            aria-hidden="true"
            title="Show / hide History"
          ></i>{" "}
        </button>
      </nav>
      <section className="history-body">
        <header className="history-header">
          <h5>Search history</h5>
          <span className="badge">{`Count : ${history.length}`}</span>
        </header>
        {history && history.length > 0 ? (
          <ul className="history-list">
            {history.map((item, index) => (
              <li className="history-list-item" key={index}>
                <section className="history-list-data">
                  <p className="history-item-text">{item.word}</p>
                  <time className="history-item-date">
                    <strong>Date: </strong>
                    {item.date} - {item.time}
                  </time>
                </section>
                <button
                  type="button"
                  className="history-delete-btn"
                  onClick={() => onDeleteHistoryItem(item)}
                >
                  <i className="fa fa-times" />
                </button>
              </li>
            ))}
          </ul>
        ) : (
          <p className="empty-history"> There is not any data. </p>
        )}
        {history && history.length > 0 && (
          <footer className="history-footer">
            <button
              type="button"
              className="history-delete-all"
              onClick={() => onDeleteAll()}
            >
              Clear search history
            </button>
          </footer>
        )}
      </section>
    </aside>
  );
};

export default SearchHistory;
